import { FC } from "react";
import {
    FormGroup,
    Anchor,
    Button,
    Text,
} from "@doar/components";
import { useForm } from "react-hook-form";
import {
    StyledWrap,
    StyledTitle,
    StyledDesc,
    StyledBottomText,
    Container,
    Row
} from "../signup-form/style";
import Signup1Form from "../profile_forms/signup1";



interface SignUpProps{
    handleChange?:any
}


interface IFormValues {
    email: string;
    password: string;
    first_name: string;
    last_name: string;
}

const ProfilePersonalInfoForm: FC<SignUpProps> = () => {
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();

    const onSubmit = (data: IFormValues) => {
        console.log(JSON.stringify(data, null));
    };

    return (
        <StyledWrap>
            <StyledTitle>Update your account infos</StyledTitle>
            <StyledDesc>
                {/*It&apos;s free to signup and only takes a minute.*/}
            </StyledDesc>
            <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
                <Signup1Form register={register} errors={errors} />
                <Container>
                    <Row sm="100" md="100" lg="100" xl="100">
                        <FormGroup>
                            <Button type="submit" color="brand2" fullwidth>
                                Update
                            </Button>
                        </FormGroup>
                    </Row>
                </Container>
            </form>
        </StyledWrap>
    );
};

export default ProfilePersonalInfoForm;


